<template>
  <div class="widgetContainer widgetContainer--center widgetContainer--scrollable businessDetails">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <span
          v-if="$businessChecking === true && $personalChecking === true"
          class="icon-Arrow-big-left cancel"
          @click="back" />
        <span v-else>&nbsp;</span>
        <p class="title">
          {{ $t('kybEtv_NavTitle') }}
        </p>
      </div>
    </div>
    <div class="widgetContainer__body">
      <el-form
        @keyup.enter.native="submitForm()"
        :model="business"
        ref="businessForm"
        :rules="formRules"
        @submit.prevent.native
        class="wise-form">
        <el-row direction="column" :gutter="12">
          <div v-for="(g, i) in fields" :key="`group-${g.groupName}`" :style="{ paddingTop: i !== 0 ? '18px' : '0', paddingBottom: '18px' }">
            <el-col :span="24">
              <span class="groupName-label">{{ g.groupName }}</span>
            </el-col>
            <el-col :span="24">
              <el-row direction="column" :gutter="28">
                <div v-for="f in g.fields" :key="`field-${f.name}`">
                  <el-col :span="24">
                    <etv-input
                      :default-value="getDefaultValue(g.key,f.key)"
                      :name="f.name"
                      :key-name="'etvValue.'+g.key+'.'+f.key"
                      :options="fieldOptionsMap[f.key]"
                      @change="({value, field}) => handleInputChange({value, field, groupKey: g.key, fieldKey: f.key})" />
                  </el-col>
                </div>
              </el-row>
            </el-col>
          </div>
        </el-row>
      </el-form>
      <div class="widgetContainer__footer--fixed">
        <el-button type="primary" class="el-button__brand brand" @click="submitForm()" :disabled="!isFormValid">
          {{ $t('kyb_NavRightButton_Title') }}
        </el-button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';
import kyb from '../mixins/kyb';
import EtvInput from '@/components/EtvInput.vue';
import {getETVFieldOptionsMap, getEtvFields, EtvPlainToOption} from '@m/business/utills/business'
export default {
  name: 'BusinessDetails',
  mixins: [kyb],
  components: {
    EtvInput,
  },
  created() {
    /** @type {import('@m/kyb/store/state').kybState['selectedBusinessType'] } */
    const business = this.getSelectedBusiness;
    const loader = this.showLoader();
   
    const initialEtvValue = {};
    this.fields.forEach((g) => {
      const obj = {};
      g.fields.forEach((f) => {
        obj[f.key] = {
          count: '',
          amount: ''
        };
      });
      initialEtvValue[g.key] = obj;
    });

    this.business.etvValue = initialEtvValue;

    this.getProjection(business.id).then((res) => {
      if(res.transactions && res.transactions.annual) {
        const v = EtvPlainToOption(res.transactions.annual)
        console.log(res.transactions.annual, v)
        this.business.etvValue = res.transactions.annual;
      }
     
    }).finally(() => {
      loader.close();
    });
    
  },
  computed: {
    ...mapGetters('kyb', ['getSelectedBusiness', 'getNacisCodeData']),
    ...mapGetters('user', ['getPersonDetails']),
    ...mapGetters('account', ['getAllAccount']),
    isFormValid() {
      const etvData = this.business.etvValue;
     
      if (etvData) {
        return Object.keys(etvData).every((key) => {
          const group = etvData[key];
          return Object.keys(group).every((k) => group[k].amount && group[k].amount !== '' && group[k].count && group[k].count !== '');
        });
      }
      return false;
    },
    fieldOptionsMap() {
      return getETVFieldOptionsMap()
    },
    fields() {
      return getEtvFields();
    }
  },
  data() {
    return {
      /** @type {import('@m/kyb/store/state').kybState['selectedBusinessType]}*/
      business: {
        etvValue: null
      },
      formRules: {
       
      },
    };
  },
  methods: {
    ...mapActions('kyb', [
      'updateProjection',
      'listAllBusiness',
      'getProjection'
    ]),
    ...mapMutations('kyb', ['setSelectedBusiness']),
    back() {
      this.goToPreviousScreen()
    },
    submitForm() {
      this.$refs.businessForm.validate((valid) => {
        if (!valid) return;

        /** @type {import('@m/kyb/store/state').kybState['selectedBusinessType']}*/
        const selectedBusiness = this.getSelectedBusiness;
        const etvData = this.business.etvValue;
        const etvDataCloned = JSON.parse(JSON.stringify(etvData));
        // prepare data
        Object.keys(etvDataCloned).forEach((key) => {
          const group = etvDataCloned[key];
          Object.keys(group).forEach((k) => {
            group[k].amount = this.findMaximumFromText(group[k].amount);
            group[k].count = this.findMaximumFromText(group[k].count);
          });
        });

        const loader = this.showLoader();
        this.updateProjection({payload : {transactions: {
          annual: etvDataCloned
        }}, businessId: selectedBusiness.id })
          .then(() => {
            this.$router.push('/kyb/businessAddress');
          })
          .catch((e) => {
            console.log(e);
            this.apiErrorMessage(e);
          })
          .finally(() => {
            loader.close();
          });
      });
    },
    handleInputChange({value, field, groupKey, fieldKey}) {
      const clonedValue = JSON.parse(JSON.stringify(this.business.etvValue));
      if (clonedValue[groupKey] && clonedValue[groupKey][fieldKey]) {
        clonedValue[groupKey][fieldKey][field] = value;
        this.business.etvValue = clonedValue;
      }
    },
    findMaximumFromText (text) {
      const parts = text.split('-');
      const maximum = parts[parts.length - 1].replaceAll('$', '').replaceAll(',', '').replaceAll(' ', '');
      if (maximum.includes('+')) {
        let maximumNumber = parseFloat(maximum);
        if (!Number.isNaN(maximumNumber)) {
          maximumNumber += 1;
          return `${maximumNumber}`;
        }
      }
      return maximum;
    },
    getDefaultValue(gKey, fKey) {
      
      const d =  this.business.etvValue && this.business.etvValue[gKey] && this.business.etvValue[gKey][fKey]
      return d;
    }
  }
};
</script>
<style lang="scss">
.businessDetails {
  .groupName-label {
    margin-bottom: 10px;
    display: inline-block;
  }
}
</style>