<template>
  <div class="widgetContainer widgetContainer--center widgetContainer--scrollable businessDetails">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <span class="icon-Arrow-big-left cancel" @click="goToPreviousScreen" />
        <p class="title">
          {{ $t('kyb_address_NavTitle') }}
        </p>
      </div>
    </div>

    <div class="widgetContainer__body">
      <el-form
        class="wise-form"
        ref="personAddress"
        :model="formData"
        :rules="formRules">
        <div class="el-input--full-width el-input">
          <el-form-item
            prop="address.line1"
            :rules="formRules.line1"
            class="is-no-asterisk"
            :label="$t('address_1')">
            <el-input style="display: none" />
            <el-input v-model="formData.address.line1" style="display: none" />
            <div class="el-input">
              <gmap-autocomplete
                :placeholder="$t('address_1')"
                autocomplete="disabled"
                :value="formData.address.line1"
                :options="{
                  componentRestrictions: { country: country }
                }"
                @change.native="formData.address.line1 = $event.target.value"
                @place_changed="updateAddress($event)"
                :select-first-on-enter="true"
                class="el-input__inner" />
            </div>
          </el-form-item>
        </div>

        <el-form-item prop="address.line2" :label="$t('address_2')">
          <el-input
            :placeholder="$t('address_2')"
            v-model="formData.address.line2" />
        </el-form-item>
        <div class="wise-form__inline">
          <el-form-item
            prop="address.city"
            class="is-no-asterisk"
            :rules="formRules.city"
            :label="$t('address_City')">
            <el-input
              style="flex: 2"
              :placeholder="$t('address_City')"
              v-model="formData.address.city" />
          </el-form-item>
          <el-form-item
            prop="address.country"
            class="is-no-asterisk"
            :rules="formRules.state"
            :label="$t('address_State')">
            <el-input
              style="flex: 1"
              :placeholder="$t('address_State')"
              v-model="formData.address.state" />
          </el-form-item>
          <el-form-item
            prop="address.postalCode"
            class="is-no-asterisk"
            :rules="formRules.postalCode"
            :label="$t('address_Zipcode')">
            <el-input
              style="flex: 1"
              :placeholder="$t('address_Zipcode')"
              v-model="formData.address.postalCode" />
          </el-form-item>
        </div>
      </el-form>
      <div class="widgetContainer__footer--fixed">
        <el-button type="primary" class="el-button__brand brand" @click="next()" :disabled="isFormValid">
          {{ $t('kyb_NavRightButton_Title') }}
        </el-button>
      </div>
    </div>
  </div>
</template>
<script>
import { formatGoogleMapData } from '@/utils/gmap';
import { mapActions, mapGetters, mapMutations } from 'vuex';
import kyb from '../mixins/kyb';
import { getCountryDetailsFromPhoneNumber } from '@/utils/env';

export default {
  name: 'BusinessAddress',
  mixins: [kyb],
  data() {
    return {
      formData: {
        address: {
          line1: null,
          line2: null,
          city: null,
          postalCode: null,
          country: 'US',
          addressType: 'mailing',
          state: null
        }
      },
      formRules: {
        line1: [
          {
            required: true,
            message: 'Address 1 is required'
          }
        ],
        city: [
          {
            required: false
          }
        ],
        state: [
          {
            required: false
          }
        ],
        postalCode: [
          {
            required: true,
            message: 'Zip Code is required'
          }
        ]
      }
    };
  },
  computed: {
    ...mapGetters('user', ['getPersonDetails']),
    ...mapGetters('kyb', ['getSelectedBusiness']),
    isFormValid() {
      let cityRequired = '';
      if(this.formRules.city[0].required)
        cityRequired = this.formData.address.city;
      else
        cityRequired = true;

      let stateRequired = '';
      if(this.formRules.city[0].required)
        stateRequired = this.formData.address.state;
      else
        stateRequired = true;

      if (
        this.formData.address.line1 &&
        cityRequired &&
        stateRequired &&
        this.formData.address.postalCode
      ) {
        return false;
      } else {
        return true;
      }
    },
    country() {
      /** @type {import('@/modules/user/store/state').userState['personDetails']} */
      const person = this.getPersonDetails;
      const details = getCountryDetailsFromPhoneNumber(person.phone);
      if(details && details.code)
        return details.code;
      
      return 'US'

    }
  },
  created() {
    /** @type {import('@m/kyb/store/state').kybState['selectedBusinessType'] } */
    const business = this.getSelectedBusiness;
    if (business && business.id) {
      const country = this.country;
      this.setCityStateRequired(country);
      if(business.address) {
        const { line1, line2, city, postalCode, state } = business.address;
        this.formData.address = {
          line1,
          line2,
          city,
          postalCode,
          country: 'US',
          addressType: 'legal',
          state
        };
      }
    } else {
      const loader = this.showLoader();
      this.listAllBusiness()
        .then((r) => {
          if (r.total !== 0) {
            /** @type {import('@m/kyb/store/state').kybState['bussinessDetails']} */
            const businessList = r.data;
            this.setSelectedBusiness(businessList[0]);
          }
        })
        .catch((e) => {
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          loader.close();
        });
    }
  },
  methods: {
    ...mapActions('kyb', ['updateBusiness', 'listAllBusiness', 'submitKyb', 'updateOwner', 'fetchAllOwners']),
    ...mapMutations('kyb', ['setSelectedBusiness']),
    setCityStateRequired(v) {
      if(v === 'US') {
        this.formRules.city = [
          {
            required: true,
            message: 'City is required'
          }
        ];
        this.formRules.state = [
          {
            required: true,
            message: 'State is required'
          }
        ];
      }
    },
    updateAddress(e) {
      const address = formatGoogleMapData(e);
      this.formData.address = address;
    },
    next() {
      this.$refs.personAddress.validate((valid) => {
        if (!valid) return;

        /** @type {import('@m/kyb/store/state').kybState['selectedBusinessType'] } */
        const business = {
          ...this.getSelectedBusiness
        };

        const { id, legalName, entityType, dba, idNumber, idType } = business;

        const { line1, line2, city, postalCode, state } =
          this.formData.address;
        
        const country = this.country;

        const payload = {
          businessId: id,
          business: {
            legalName,
            entityType,
            dba,
            idNumber,
            idType,
            address: {
              line1,
              line2,
              city,
              postalCode,
              country,
              addressType: 'legal',
              state
            }
          }
        };
        const loader = this.showLoader();
        this.updateBusiness(payload)
          .then(() => {
            setTimeout(() => {
              if (!['soleProprietor', 'singleMemberLLC'].includes(
                this.getSelectedBusiness.entityType
              )) {
                this.$router.push('/kyb/ownership')
              }  else {
                this.$router.push('/kyb/ownersList');
              }
            }, 0);
          })
          .catch((e) => {
            console.log(e);
            this.apiErrorMessage(e);
          })
          .finally(() => {
            loader.close();
          });
      });
    }
  },
  watch: {
    country: function (newVal) {
      this.setCityStateRequired(newVal);
    }
  }
};
</script>
