<template>
  <div class="">
    <el-row :gutter="20" type="flex" justify="space-between" class="label-row">
      <el-col :span="16">
        <span class="el-form-item__label">{{ name }}</span>
      </el-col>
      <el-col :span="8">
        <span class="el-form-item__label">Count:</span>
        <!--  <span class="text-dark">{{ selectedValue ? selectedValue.count : '' }}</span> -->
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="16">
        <el-form-item :rules="{required: true, message: 'Required.'}" :prop="keyName+'.amount'">
          <el-select
            class="form-control-1"
            v-model="value"
            placeholder="Select Amount"
            @change="handleChange($event, 'amount')">
            <el-option
              v-for="option in options"
              :key="option.amount"
              :value="option.amount">
              <div class="option-grid">
                <div class="title">
                  {{ option.amount }}
                </div>
              </div>
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <el-form-item :rules="{required: true, message: 'Required.'}" :prop="keyName+'.count'">
          <el-select
            class="form-control-1"
            v-model="count"
            placeholder="Select"
            :disabled="value.replaceAll(' ', '') == '$0.00-$0.00' "
            @change="handleChange($event, 'count')">
            <el-option
              v-for="option in options"
              :key="option.count"
              :value="option.count">
              <div class="option-grid">
                <div class="title">
                  {{ option.count }}
                </div>
              </div>
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <!--
    <el-slider
      v-model="sliderValue"
      :min="min"
      :max="max"
      @change="handleSliderChange"
    ></el-slider>
    -->
  </div>
</template>

<script>

import { Select, Row, Col, Option, FormItem} from 'element-ui';
export default {
  props: {
    name: String,
    keyName: String,
    defaultValue: {
      type: Object,
      default: null,
    },
    options: Array,
  },
  components: {
    [Select.name]: Select,
    [Option.name]: Option,
    [Row.name]: Row,
    [Col.name]: Col,
    [FormItem.name]: FormItem,
  },  
  data() {
    return {
      value: '',
      count: '',
    };
  },
  computed: {
    fieldOptions() {
      const o =  this.options.map((v) => ({
        value: v,
        label: v,
      })) || [];
      return o
    },
  },
  methods: {
    handleChange(value, field) {
      if(field == 'amount') {
        this.value = value;
        if('$0.00-$0.00' === value.replaceAll(' ', '')) {
          this.count = '0 - 0';
          this.$emit('change',{value: '0 - 0', field: 'count'});
        }
      } else {
        this.count = value;
      }
      this.$emit('change',{value: value, field});
    },
  },
  mounted() {
    if(this.defaultValue) {
      this.value = this.defaultValue.amount
      this.count = this.defaultValue.count
    }
  },
  watch: {
    defaultValue(v) {
      this.value = v.amount
      this.count = v.count
    }
  }
};
</script>
